"use client"

import React, { useState, Suspense } from "react"
import { useRouter } from "next/navigation"
import { useSearchParams } from "next/navigation"
import { 
  ArrowDownToLine, 
  QrCode,
  Copy,
  Check,
  Clock,
  CheckCircle2,
  XCircle,
  Bitcoin,
  Loader2,
  Lock,
  Eye,
  EyeOff,
  Smartphone,
  ChevronLeft,
  ChevronRight,
  Info,
  RefreshCw
} from "lucide-react"

type PaymentMethod = "pix" | "crypto"

const depositHistory = [
  { id: "DEP001", method: "pix" as const, amount: "R$ 1.500,00", fee: "R$ 7,50", status: "completed", date: "20 Jan 2026, 14:32" },
  { id: "DEP002", method: "crypto" as const, currency: "USDT", amount: "R$ 2.000,00", fee: "R$ 20,00", status: "completed", date: "19 Jan 2026, 09:20" },
  { id: "DEP003", method: "pix" as const, amount: "R$ 500,00", fee: "R$ 2,50", status: "completed", date: "18 Jan 2026, 16:45" },
  { id: "DEP004", method: "crypto" as const, currency: "BTC", amount: "R$ 3.000,00", fee: "R$ 30,00", status: "pending", date: "17 Jan 2026, 11:30" },
  { id: "DEP005", method: "pix" as const, amount: "R$ 5.000,00", fee: "R$ 25,00", status: "completed", date: "15 Jan 2026, 08:15" },
]

const statusConfig = {
  completed: { icon: CheckCircle2, color: "text-emerald-400", bg: "bg-emerald-500/10", label: "Concluido" },
  pending: { icon: Clock, color: "text-amber-400", bg: "bg-amber-500/10", label: "Pendente" },
  failed: { icon: XCircle, color: "text-red-400", bg: "bg-red-500/10", label: "Falhou" },
}

const feeRates = {
  pix: { base: 0.5, label: "0,5%" },
  crypto: { base: 1.0, label: "1,0%" },
}

const CRYPTO_OPTIONS = [
  { id: "usdt_trc20", name: "USDT", network: "TRC20", address: "TN8gJ7xZ2K9mQ5wEr3Yb6Pm1Vq8Nc4Lj" },
  { id: "usdt_erc20", name: "USDT", network: "ERC20", address: "0x742d35Cc6634C0532925a3b844Bc9e7595f8bF2a" },
  { id: "btc", name: "Bitcoin", network: "BTC", address: "bc1qxy2kgdygjrsqtzq2n0yrfz2493p83kkfjhx0wlh" },
  { id: "eth", name: "Ethereum", network: "ETH", address: "0x742d35Cc6634C0532925a3b844Bc9e7595f8bF2a" },
  { id: "ltc", name: "Litecoin", network: "LTC", address: "ltc1qhz8gxkmjc7t3knhfvz9fzqjxlqkzpxrjnqy7e4" },
]

function DepositosContent() {
  const searchParams = useSearchParams()
  const initialMethod = searchParams.get("metodo") as PaymentMethod | null
  const router = useRouter()

  const [selectedMethod, setSelectedMethod] = useState<PaymentMethod | null>(initialMethod)
  const [amount, setAmount] = useState("")
  const [step, setStep] = useState<"method" | "form" | "pin" | "confirm" | "processing" | "success">(initialMethod ? "form" : "method")
  const [copied, setCopied] = useState(false)
  const [pin, setPin] = useState("")
  const [showPin, setShowPin] = useState(false)
  const [isProcessing, setIsProcessing] = useState(false)
  const [selectedCrypto, setSelectedCrypto] = useState<string>("usdt_trc20")
  const [transactionId, setTransactionId] = useState("")
  const [activeTab, setActiveTab] = useState<"depositar" | "historico">("depositar")
  const transactionCode = "00020126580014br.gov.bcb.pix0136a1b2c3d4-e5f6-7890-abcd-ef1234567890"

  const numericAmount = Number.parseFloat(amount.replace(/\D/g, "")) / 100 || 0
  const feePercent = selectedMethod ? feeRates[selectedMethod].base : 0.5
  const fee = numericAmount * (feePercent / 100)
  const netAmount = numericAmount - fee

  const formatCurrency = (value: string) => {
    const numbers = value.replace(/\D/g, "")
    return (Number.parseInt(numbers || "0") / 100).toLocaleString("pt-BR", { style: "currency", currency: "BRL" })
  }

  const handleAmountChange = (e: React.ChangeEvent<HTMLInputElement>) => setAmount(formatCurrency(e.target.value))

  const handleCopy = (text: string) => {
    navigator.clipboard.writeText(text)
    setCopied(true)
    setTimeout(() => setCopied(false), 2000)
  }

  const handleMethodChange = (method: PaymentMethod) => {
    setSelectedMethod(method)
    setStep("form")
    setAmount("")
    setPin("")
    setSelectedCrypto("usdt_trc20")
  }

  const handleFormSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    if (numericAmount > 0) {
      setStep("pin")
    }
  }

  const handlePinSubmit = () => {
    if (pin.length === 6) setStep("confirm")
  }

  const handleConfirm = async () => {
    setStep("processing")
    setIsProcessing(true)

    try {
      const response = await fetch("/api/deposits", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "Authorization": `Bearer ${localStorage.getItem("authToken")}`
        },
        body: JSON.stringify({
          metodo: selectedMethod,
          valor: numericAmount,
          pin,
          cryptoCurrency: selectedMethod === "crypto" ? selectedCrypto : undefined
        })
      })

      const data = await response.json()

      if (data.success) {
        setTransactionId(data.deposit?.id || `DEP${Date.now()}`)
        setStep("success")
      } else {
        alert(data.error || "Erro ao processar deposito")
        setStep("confirm")
      }
    } catch {
      alert("Erro de conexao")
      setStep("confirm")
    } finally {
      setIsProcessing(false)
    }
  }

  const resetForm = () => {
    setSelectedMethod(null)
    setAmount("")
    setStep("method")
    setPin("")
    setTransactionId("")
  }

  const goBack = () => {
    if (step === "form") {
      setStep("method")
      setSelectedMethod(null)
    } else if (step === "pin") {
      setStep("form")
      setPin("")
    } else if (step === "confirm") {
      setStep("pin")
    }
  }

  const currentCrypto = CRYPTO_OPTIONS.find(c => c.id === selectedCrypto)

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-xl sm:text-2xl font-semibold text-white">Depositos</h1>
        <p className="text-zinc-500 text-xs sm:text-sm mt-1">Adicione saldo via PIX ou Crypto</p>
      </div>

      <div className="flex gap-1 p-1 bg-zinc-900/50 rounded-xl w-fit">
        {[
          { id: "depositar" as const, label: "Depositar" },
          { id: "historico" as const, label: "Historico" },
        ].map((tab) => (
          <button
            key={tab.id}
            type="button"
            onClick={() => { setActiveTab(tab.id); if (tab.id === "depositar") resetForm() }}
className={`px-3 sm:px-4 py-2 text-xs sm:text-sm font-medium rounded-lg transition-colors whitespace-nowrap ${
                  activeTab === tab.id ? "bg-zinc-800 text-white" : "text-zinc-400 hover:text-white"
                }`}
          >
            {tab.label}
          </button>
        ))}
      </div>

      {activeTab === "depositar" && (
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          <div className="lg:col-span-2">
            {/* Step 1: Select Method */}
            {step === "method" && (
              <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-4 sm:p-6">
                <h2 className="text-white font-medium mb-3 sm:mb-4 text-sm sm:text-base">Selecione o metodo</h2>
                <div className="grid grid-cols-2 gap-3 sm:gap-4">
                  <button
                    type="button"
                    onClick={() => handleMethodChange("pix")}
                    className="flex flex-col items-center gap-2 sm:gap-3 p-4 sm:p-6 bg-zinc-800/30 hover:bg-zinc-800 border border-zinc-700 hover:border-emerald-500/50 rounded-xl transition-all group"
                  >
                    <div className="w-10 h-10 sm:w-14 sm:h-14 rounded-xl bg-emerald-500/10 group-hover:bg-emerald-500/20 flex items-center justify-center transition-colors">
                      <Smartphone className="w-5 h-5 sm:w-7 sm:h-7 text-emerald-400" />
                    </div>
                    <div className="text-center">
                      <p className="text-white font-medium text-sm sm:text-base">PIX</p>
                      <p className="text-zinc-500 text-xs mt-0.5 sm:mt-1 hidden sm:block">Deposito instantaneo</p>
                      <p className="text-emerald-400 text-xs sm:text-sm font-semibold mt-1 sm:mt-2">0,5%</p>
                    </div>
                  </button>

                  <button
                    type="button"
                    onClick={() => handleMethodChange("crypto")}
                    className="flex flex-col items-center gap-2 sm:gap-3 p-4 sm:p-6 bg-zinc-800/30 hover:bg-zinc-800 border border-zinc-700 hover:border-orange-500/50 rounded-xl transition-all group"
                  >
                    <div className="w-10 h-10 sm:w-14 sm:h-14 rounded-xl bg-orange-500/10 group-hover:bg-orange-500/20 flex items-center justify-center transition-colors">
                      <Bitcoin className="w-5 h-5 sm:w-7 sm:h-7 text-orange-400" />
                    </div>
                    <div className="text-center">
                      <p className="text-white font-medium text-sm sm:text-base">Crypto</p>
                      <p className="text-zinc-500 text-xs mt-0.5 sm:mt-1 hidden sm:block">BTC, ETH, USDT, LTC</p>
                      <p className="text-orange-400 text-xs sm:text-sm font-semibold mt-1 sm:mt-2">1,0%</p>
                    </div>
                  </button>
                </div>
              </div>
            )}

            {/* Step 2: Form */}
            {step === "form" && (
              <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-4 sm:p-6">
                <button type="button" onClick={goBack} className="flex items-center gap-1 text-zinc-400 hover:text-white text-sm mb-4 transition-colors">
                  <ChevronLeft className="w-4 h-4" />
                  Voltar
                </button>

                <div className={`flex items-center gap-2 sm:gap-3 mb-4 sm:mb-6 p-2.5 sm:p-3 rounded-lg border ${
                  selectedMethod === "pix" 
                    ? "bg-emerald-500/10 border-emerald-500/20"
                    : "bg-orange-500/10 border-orange-500/20"
                }`}>
                  {selectedMethod === "pix" ? (
                    <Smartphone className="w-4 h-4 sm:w-5 sm:h-5 text-emerald-400" />
                  ) : (
                    <Bitcoin className="w-4 h-4 sm:w-5 sm:h-5 text-orange-400" />
                  )}
                  <span className="text-white font-medium text-sm sm:text-base">
                    {selectedMethod === "pix" ? "PIX" : "Crypto"}
                  </span>
                  <span className={`ml-auto text-xs sm:text-sm font-medium ${
                    selectedMethod === "pix" ? "text-emerald-400" : "text-orange-400"
                  }`}>
                    {feeRates[selectedMethod!].label}
                  </span>
                </div>

                <form onSubmit={handleFormSubmit}>
                  {/* Crypto Selection */}
                  {selectedMethod === "crypto" && (
                    <div className="mb-4 sm:mb-6">
                      <label className="block text-white font-medium text-sm sm:text-base mb-2 sm:mb-3">Selecione a criptomoeda</label>
                      <div className="grid grid-cols-2 sm:grid-cols-3 gap-2">
                        {CRYPTO_OPTIONS.map((crypto) => (
                          <button
                            key={crypto.id}
                            type="button"
                            onClick={() => setSelectedCrypto(crypto.id)}
                            className={`p-2 sm:p-3 rounded-lg border text-left transition-colors ${
                              selectedCrypto === crypto.id
                                ? "bg-orange-500/20 border-orange-500/30 text-orange-400"
                                : "bg-zinc-800/50 border-zinc-700 text-zinc-400 hover:border-zinc-600"
                            }`}
                          >
                            <p className="font-medium text-xs sm:text-sm">{crypto.name}</p>
                            <p className="text-xs opacity-70 hidden sm:block">{crypto.network}</p>
                          </button>
                        ))}
                      </div>
                    </div>
                  )}

                  {/* Amount */}
                  <h2 className="text-white font-medium text-sm sm:text-base mb-3 sm:mb-4">Valor do deposito</h2>
                  <div className="relative mb-3 sm:mb-4">
                    <input
                      type="text"
                      value={amount}
                      onChange={handleAmountChange}
                      placeholder="R$ 0,00"
                      className="w-full px-4 py-3 sm:py-4 bg-zinc-800 border border-zinc-700 rounded-xl text-white text-xl sm:text-2xl font-semibold placeholder:text-zinc-600 focus:outline-none focus:border-emerald-500/50 transition-colors text-center"
                    />
                  </div>

                  <div className="flex flex-wrap gap-1.5 sm:gap-2 mb-4 sm:mb-6">
                    {[50, 100, 200, 500, 1000].map((value) => (
                      <button
                        key={value}
                        type="button"
                        onClick={() => setAmount(formatCurrency(String(value * 100)))}
                        className="px-3 sm:px-4 py-1.5 sm:py-2 bg-zinc-800 hover:bg-zinc-700 text-zinc-300 text-xs sm:text-sm rounded-lg transition-colors"
                      >
                        R$ {value}
                      </button>
                    ))}
                  </div>

                  {/* Fee Summary */}
                  {numericAmount > 0 && (
                    <div className="space-y-2 mb-4 sm:mb-6 p-3 sm:p-4 bg-zinc-800/30 rounded-lg">
                      <div className="flex justify-between text-xs sm:text-sm">
                        <span className="text-zinc-500">Valor</span>
                        <span className="text-white">R$ {numericAmount.toFixed(2)}</span>
                      </div>
                      <div className="flex justify-between text-xs sm:text-sm">
                        <span className="text-zinc-500">Taxa ({feeRates[selectedMethod!].label})</span>
                        <span className="text-zinc-400">- R$ {fee.toFixed(2)}</span>
                      </div>
                      <div className="flex justify-between text-xs sm:text-sm pt-2 border-t border-zinc-700">
                        <span className="text-zinc-400 font-medium">Recebera</span>
                        <span className={`font-semibold ${selectedMethod === "pix" ? "text-emerald-400" : "text-orange-400"}`}>
                          R$ {netAmount.toFixed(2)}
                        </span>
                      </div>
                    </div>
                  )}

                  <button
                    type="submit"
                    disabled={numericAmount <= 0}
                    className={`w-full flex items-center justify-center gap-2 px-4 sm:px-6 py-2.5 sm:py-3 font-medium text-sm sm:text-base rounded-xl transition-colors disabled:opacity-50 disabled:cursor-not-allowed ${
                      selectedMethod === "pix"
                        ? "bg-emerald-500 hover:bg-emerald-400 text-zinc-900"
                        : "bg-orange-500 hover:bg-orange-400 text-zinc-900"
                    }`}
                  >
                    Continuar
                    <ChevronRight className="w-4 h-4 sm:w-5 sm:h-5" />
                  </button>
                </form>
              </div>
            )}

            {/* Step 3: PIN */}
            {step === "pin" && (
              <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-4 sm:p-6">
                <button type="button" onClick={goBack} className="flex items-center gap-1 text-zinc-400 hover:text-white text-sm mb-4 transition-colors">
                  <ChevronLeft className="w-4 h-4" />
                  Voltar
                </button>

                <div className="flex items-center gap-2 sm:gap-3 mb-4 sm:mb-6">
                  <div className={`w-8 h-8 sm:w-10 sm:h-10 rounded-xl flex items-center justify-center ${
                    selectedMethod === "pix" ? "bg-emerald-500/10" : "bg-orange-500/10"
                  }`}>
                    <Lock className={`w-4 h-4 sm:w-5 sm:h-5 ${selectedMethod === "pix" ? "text-emerald-400" : "text-orange-400"}`} />
                  </div>
                  <div>
                    <h2 className="text-white font-medium text-sm sm:text-base">Confirme com seu PIN</h2>
                    <p className="text-zinc-500 text-xs sm:text-sm">Digite seu PIN de 6 digitos</p>
                  </div>
                </div>

                <div className="max-w-xs mx-auto">
                  <div className="relative mb-4 sm:mb-6">
                    <input
                      type={showPin ? "text" : "password"}
                      value={pin}
                      onChange={(e) => setPin(e.target.value.replace(/\D/g, "").slice(0, 6))}
                      placeholder="******"
                      maxLength={6}
                      className="w-full px-4 py-3 sm:py-4 pr-12 bg-zinc-800 border border-zinc-700 rounded-xl text-white text-center text-xl sm:text-2xl tracking-[0.5em] font-mono placeholder:text-zinc-600 placeholder:tracking-normal focus:outline-none focus:border-emerald-500/50 transition-colors"
                    />
                    <button
                      type="button"
                      onClick={() => setShowPin(!showPin)}
                      className="absolute right-3 sm:right-4 top-1/2 -translate-y-1/2 text-zinc-500 hover:text-white"
                    >
                      {showPin ? <EyeOff className="w-4 h-4 sm:w-5 sm:h-5" /> : <Eye className="w-4 h-4 sm:w-5 sm:h-5" />}
                    </button>
                  </div>
                  <button
                    type="button"
                    onClick={handlePinSubmit}
                    disabled={pin.length !== 6}
                    className={`w-full px-4 sm:px-6 py-2.5 sm:py-3 font-medium text-sm sm:text-base rounded-xl transition-colors disabled:opacity-50 ${
                      selectedMethod === "pix"
                        ? "bg-emerald-500 hover:bg-emerald-400 text-zinc-900"
                        : "bg-orange-500 hover:bg-orange-400 text-zinc-900"
                    }`}
                  >
                    Confirmar
                  </button>
                </div>
              </div>
            )}

            {/* Step 4: Confirm with QR Code / Address */}
            {step === "confirm" && (
              <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-4 sm:p-6">
                <button type="button" onClick={goBack} className="flex items-center gap-1 text-zinc-400 hover:text-white text-sm mb-4 transition-colors">
                  <ChevronLeft className="w-4 h-4" />
                  Voltar
                </button>

                <h2 className="text-white font-medium text-sm sm:text-base mb-4 sm:mb-6 text-center">
                  {selectedMethod === "pix" ? "Escaneie ou copie o codigo" : "Envie para o endereco abaixo"}
                </h2>

                {/* QR Code */}
                <div className="flex justify-center mb-4 sm:mb-6">
                  <div className="w-36 h-36 sm:w-48 sm:h-48 bg-white rounded-xl flex items-center justify-center p-2">
                    <QrCode className="w-full h-full text-zinc-900" />
                  </div>
                </div>

                {/* Copy Code / Address */}
                <div className="mb-4 sm:mb-6">
                  <p className="text-zinc-400 text-xs sm:text-sm text-center mb-2">
                    {selectedMethod === "pix" ? "Codigo PIX" : `${currentCrypto?.name} (${currentCrypto?.network})`}
                  </p>
                  <div className="flex items-center gap-2 p-2.5 sm:p-3 bg-zinc-800 rounded-lg border border-zinc-700">
                    <code className="flex-1 text-zinc-400 text-xs truncate font-mono">
                      {selectedMethod === "pix" 
                        ? "00020126580014br.gov.bcb.pix0136a1b2c3d4-e5f6-7890-abcd-ef1234567890"
                        : currentCrypto?.address
                      }
                    </code>
                    <button 
                      type="button" 
                      onClick={() => handleCopy(selectedMethod === "pix" 
                        ? "00020126580014br.gov.bcb.pix0136a1b2c3d4-e5f6-7890-abcd-ef1234567890"
                        : currentCrypto?.address || ""
                      )} 
                      className="p-1.5 sm:p-2 hover:bg-zinc-700 rounded-lg transition-colors"
                    >
                      {copied ? <Check className={`w-4 h-4 ${selectedMethod === "pix" ? "text-emerald-400" : "text-orange-400"}`} /> : <Copy className="w-4 h-4 text-zinc-400" />}
                    </button>
                  </div>
                </div>

                {selectedMethod === "crypto" && (
                  <div className="flex items-start gap-2 sm:gap-3 mb-4 sm:mb-6 p-3 sm:p-4 bg-amber-500/10 border border-amber-500/20 rounded-xl">
                    <Info className="w-4 h-4 sm:w-5 sm:h-5 text-amber-400 flex-shrink-0 mt-0.5" />
                    <div>
                      <p className="text-amber-400 font-medium text-xs sm:text-sm">Importante</p>
                      <p className="text-amber-300/80 text-xs mt-0.5 sm:mt-1">
                        Envie apenas {currentCrypto?.name} na rede {currentCrypto?.network}.
                      </p>
                    </div>
                  </div>
                )}

                {/* Summary */}
                <div className="space-y-2 mb-4 sm:mb-6 p-3 sm:p-4 bg-zinc-800/30 rounded-lg">
                  <div className="flex justify-between text-xs sm:text-sm">
                    <span className="text-zinc-500">Metodo</span>
                    <span className="text-white">
                      {selectedMethod === "pix" ? "PIX" : currentCrypto?.name}
                    </span>
                  </div>
                  <div className="flex justify-between text-xs sm:text-sm">
                    <span className="text-zinc-500">Valor</span>
                    <span className="text-white">R$ {numericAmount.toFixed(2)}</span>
                  </div>
                  <div className="flex justify-between text-xs sm:text-sm">
                    <span className="text-zinc-500">Taxa</span>
                    <span className="text-zinc-400">- R$ {fee.toFixed(2)}</span>
                  </div>
                  <div className="flex justify-between text-xs sm:text-sm pt-2 border-t border-zinc-700">
                    <span className="text-zinc-400 font-medium">Recebera</span>
                    <span className={`font-semibold ${selectedMethod === "pix" ? "text-emerald-400" : "text-orange-400"}`}>
                      R$ {netAmount.toFixed(2)}
                    </span>
                  </div>
                </div>

                <button
                  type="button"
                  onClick={handleConfirm}
                  className={`w-full flex items-center justify-center gap-2 px-4 sm:px-6 py-2.5 sm:py-3 font-medium text-sm sm:text-base rounded-xl transition-colors ${
                    selectedMethod === "pix"
                      ? "bg-emerald-500 hover:bg-emerald-400 text-zinc-900"
                      : "bg-orange-500 hover:bg-orange-400 text-zinc-900"
                  }`}
                >
                  Ja fiz o deposito
                </button>

                {selectedMethod === "pix" && (
                  <p className="text-zinc-500 text-xs text-center mt-3 sm:mt-4">
                    Credito automatico apos confirmacao.
                  </p>
                )}
              </div>
            )}

            {/* Step 5: Processing */}
            {step === "processing" && (
              <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-8 sm:p-12 text-center">
                <Loader2 className={`w-10 h-10 sm:w-12 sm:h-12 mx-auto mb-3 sm:mb-4 animate-spin ${
                  selectedMethod === "pix" ? "text-emerald-400" : "text-orange-400"
                }`} />
                <h2 className="text-white font-medium text-base sm:text-lg mb-2">Verificando deposito</h2>
                <p className="text-zinc-500 text-xs sm:text-sm">Aguardando confirmacao...</p>
              </div>
            )}

            {/* Step 6: Success */}
            {step === "success" && (
              <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-6 sm:p-8 text-center">
                <div className={`w-12 h-12 sm:w-16 sm:h-16 rounded-full mx-auto mb-4 sm:mb-6 flex items-center justify-center ${
                  selectedMethod === "pix" ? "bg-emerald-500/20" : "bg-orange-500/20"
                }`}>
                  <CheckCircle2 className={`w-6 h-6 sm:w-8 sm:h-8 ${selectedMethod === "pix" ? "text-emerald-400" : "text-orange-400"}`} />
                </div>
                <h2 className="text-white font-medium text-lg sm:text-xl mb-2">Deposito registrado!</h2>
                <p className="text-zinc-500 text-xs sm:text-sm mb-4 sm:mb-6">
                  {selectedMethod === "pix" 
                    ? "Seu saldo sera atualizado assim que o pagamento for confirmado."
                    : "Aguardando confirmacoes na blockchain. Isso pode levar alguns minutos."
                  }
                </p>

                <div className="bg-zinc-800/50 rounded-lg p-3 sm:p-4 mb-4 sm:mb-6">
                  <p className="text-zinc-500 text-xs mb-1">Codigo</p>
                  <div className="flex items-center justify-center gap-2">
                    <code className="text-white font-mono text-xs sm:text-sm truncate">{transactionCode}</code>
                    <button type="button" onClick={() => handleCopy(transactionCode)} className="text-zinc-400 hover:text-white flex-shrink-0">
                      {copied ? <Check className="w-4 h-4 text-emerald-400" /> : <Copy className="w-4 h-4" />}
                    </button>
                  </div>
                </div>
                <button
                  type="button"
                  onClick={() => router.push("/dashboard/carteira")}
                  className="w-full px-4 sm:px-6 py-2.5 sm:py-3 bg-zinc-700 hover:bg-zinc-600 text-white font-medium text-sm sm:text-base rounded-xl transition-colors"
                >
                  Ver carteira
                </button>
              </div>
            )}
          </div>

          {/* Sidebar */}
          <div className="space-y-6">
            <div className={`border rounded-xl p-5 ${
              selectedMethod === "pix" 
                ? "bg-emerald-500/5 border-emerald-500/20" 
                : selectedMethod === "crypto"
                ? "bg-orange-500/5 border-orange-500/20"
                : "bg-zinc-900/50 border-zinc-800"
            }`}>
              <h3 className="text-white font-medium mb-3">
                {selectedMethod === "pix" ? "Sobre PIX" : selectedMethod === "crypto" ? "Sobre Crypto" : "Informacoes"}
              </h3>
              {selectedMethod === "pix" && (
                <ul className="space-y-2 text-zinc-400 text-sm">
                  <li className="flex items-start gap-2">
                    <CheckCircle2 className="w-4 h-4 text-emerald-400 mt-0.5 flex-shrink-0" />
                    Deposito instantaneo 24/7
                  </li>
                  <li className="flex items-start gap-2">
                    <CheckCircle2 className="w-4 h-4 text-emerald-400 mt-0.5 flex-shrink-0" />
                    Taxa de apenas 0,5%
                  </li>
                  <li className="flex items-start gap-2">
                    <CheckCircle2 className="w-4 h-4 text-emerald-400 mt-0.5 flex-shrink-0" />
                    QR Code valido por 30 minutos
                  </li>
                  <li className="flex items-start gap-2">
                    <CheckCircle2 className="w-4 h-4 text-emerald-400 mt-0.5 flex-shrink-0" />
                    Sem limite minimo
                  </li>
                </ul>
              )}
              {selectedMethod === "crypto" && (
                <ul className="space-y-2 text-zinc-400 text-sm">
                  <li className="flex items-start gap-2">
                    <CheckCircle2 className="w-4 h-4 text-orange-400 mt-0.5 flex-shrink-0" />
                    Suporte a BTC, ETH, USDT, LTC
                  </li>
                  <li className="flex items-start gap-2">
                    <CheckCircle2 className="w-4 h-4 text-orange-400 mt-0.5 flex-shrink-0" />
                    Taxa de 1,0%
                  </li>
                  <li className="flex items-start gap-2">
                    <CheckCircle2 className="w-4 h-4 text-orange-400 mt-0.5 flex-shrink-0" />
                    Conversao automatica para BRL
                  </li>
                  <li className="flex items-start gap-2">
                    <Info className="w-4 h-4 text-amber-400 mt-0.5 flex-shrink-0" />
                    Verifique sempre a rede correta
                  </li>
                </ul>
              )}
              {!selectedMethod && (
                <p className="text-zinc-500 text-sm">Selecione um metodo de deposito para ver mais informacoes.</p>
              )}
            </div>

            {/* Recent Deposits */}
            <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-5">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-white font-medium">Ultimos depositos</h3>
                <RefreshCw className="w-4 h-4 text-zinc-500" />
              </div>
              <div className="space-y-3">
                {depositHistory.slice(0, 3).map((deposit) => {
                  const status = statusConfig[deposit.status as keyof typeof statusConfig]
                  return (
                    <div key={deposit.id} className="flex items-center justify-between p-3 bg-zinc-800/30 rounded-lg">
                      <div className="flex items-center gap-3">
                        <div className={`w-8 h-8 rounded-lg flex items-center justify-center ${
                          deposit.method === "pix" ? "bg-emerald-500/10" : "bg-orange-500/10"
                        }`}>
                          {deposit.method === "pix" ? (
                            <Smartphone className="w-4 h-4 text-emerald-400" />
                          ) : (
                            <Bitcoin className="w-4 h-4 text-orange-400" />
                          )}
                        </div>
                        <div>
                          <p className="text-white text-sm font-medium">{deposit.amount}</p>
                          <p className="text-zinc-500 text-xs">{deposit.method === "crypto" ? deposit.currency : "PIX"}</p>
                        </div>
                      </div>
                      <div className={`flex items-center gap-1 ${status.color}`}>
                        <status.icon className="w-3 h-3" />
                        <span className="text-xs">{status.label}</span>
                      </div>
                    </div>
                  )
                })}
              </div>
            </div>
          </div>
        </div>
      )}

      {/* History Tab */}
      {activeTab === "historico" && (
        <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl">
          <div className="p-5 border-b border-zinc-800">
            <h2 className="text-lg font-semibold text-white">Historico de depositos</h2>
          </div>
          <div className="divide-y divide-zinc-800">
            {depositHistory.map((deposit) => {
              const status = statusConfig[deposit.status as keyof typeof statusConfig]
              return (
                <div key={deposit.id} className="flex items-center justify-between p-5 hover:bg-zinc-800/30 transition-colors">
                  <div className="flex items-center gap-4">
                    <div className={`w-10 h-10 rounded-xl flex items-center justify-center ${
                      deposit.method === "pix" ? "bg-emerald-500/10" : "bg-orange-500/10"
                    }`}>
                      {deposit.method === "pix" ? (
                        <Smartphone className="w-5 h-5 sm:w-7 sm:h-7 text-emerald-400" />
                      ) : (
                        <Bitcoin className="w-5 h-5 sm:w-7 sm:h-7 text-orange-400" />
                      )}
                    </div>
                    <div>
                      <p className="text-white font-medium">
                        Deposito via {deposit.method === "pix" ? "PIX" : deposit.currency}
                      </p>
                      <p className="text-zinc-500 text-sm">{deposit.date}</p>
                    </div>
                  </div>
                  <div className="text-right">
                    <p className={`font-semibold ${deposit.method === "pix" ? "text-emerald-400" : "text-orange-400"}`}>
                      + {deposit.amount}
                    </p>
                    <p className="text-zinc-600 text-xs">Taxa: {deposit.fee}</p>
                    <div className="flex items-center gap-1 justify-end mt-1">
                      <status.icon className={`w-3 h-3 ${status.color}`} />
                      <span className={`text-xs ${status.color}`}>{status.label}</span>
                    </div>
                  </div>
                </div>
              )
            })}
          </div>
        </div>
      )}
    </div>
  )
}

export default function DepositosPage() {
  return (
    <Suspense fallback={
      <div className="flex items-center justify-center min-h-[400px]">
        <Loader2 className="w-8 h-8 animate-spin text-emerald-400" />
      </div>
    }>
      <DepositosContent />
    </Suspense>
  )
}
